import styled from "styled-components";
import { ColorPallete } from "../../themes/theme";

export const HeaderComponent = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: ${ColorPallete.main};
  color: ${ColorPallete.textWhite};
  font-size: 64px;
  text-align: center;
`;

export const ChevronIcon = styled.div`
  position: absolute;
  left: 50vw;
  bottom: 5vh;
  color: ${ColorPallete.secondary};
  font-size: 25px;
  cursor: pointer;
  transition: 550ms;

  :hover {
    color: ${ColorPallete.textWhite};
  }

  a {
    color: inherit;
    text-decoration: inherit;
  }
`;

export const Languages = styled.div`
  cursor: pointer;
  opacity: 0.8;

  > img {
    max-width: 26px;
    margin-left: 1vh;
  }

  :hover {
    opacity: 1;
  }
`;

export const ContainerLanguages = styled.div`
  position: absolute;
  top: 1.5vh;
  right: 2vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 1vh;
`;
